import Vue from 'vue'
import Router from 'vue-router'
import App from "../App"

Vue.use(Router)


const Home = () => import('../page/Home/Home.vue');
const City = () => import('../page/City/City.vue');
const Location = () => import('../page/Location/Location.vue');
const Login = () => import('../page/Location/Login.vue');
const Mine = () => import('../page/Mine/Mine.vue');
const TakeOut = () => import('../page/TakeOut/TakeOut.vue');
const Orderlist = () => import('../page/Orderlist/Orderlist.vue');
const Search = () => import('../page/Search/Search.vue');
const Account = () => import('../page/Account/Account.vue');
const Vuex = () => import('../page/Vuex/Vuex.vue');
const ShiAddress = () => import('../page/ShiAddress/ShiAddress.vue');
const NewAddress = () => import('../page/NewAddress/NewAddress.vue');
const searAddre = () => import('../page/searAddre/searAddre.vue');
const Logpass = () => import('../page/Account/Logpass.vue');
const Product = () => import('../page/TakeOut/Product.vue');
const A = () => import('../page/TakeOut/A.vue');
// const EvaluateList = () => import('../page/TakeOut/evaluateList.vue');
const Confirm = () => import('../page/TakeOut/Confirm.vue');
const Order = () => import('../page/TakeOut/Order.vue');


export default new Router({
  
  routes: [
    {
        path: '/',
        redirect:'/home/takeout'
    },
    {
      path:"/home",
      component:Home,
      children:[
        {
          path:"mine",
          component:Mine
        },
        {
          path:"takeout",
          component:TakeOut
        },
        {
          path:"orderlist",
          component:Orderlist
        },
        {
          path:"search",
          component:Search
        },
        {
          path:"account",
          component:Account
        },
        {
          path:"shiAddress",
          component:ShiAddress
        },
        {
          path:"newAddress",
          component:NewAddress
        },
        {
          path:"searAddre",
          component:searAddre
        },
        {
          path:"logpass",
          component:Logpass
        },
        {
          path:"product",
          component:Product
        },
        {
          path:"order",
          component:Order
        },
        {
          path:"confirm",
          component:Confirm
        },
      ]
    },
    {
      path:"/city",
      component:City
    },
    {
      path:"/location",
      component:Location
    },
    {
      path:"/login",
      component:Login
    },
    {
      path:"/vuex",
      component:Vuex
    },
   
  ]
})



