import Vue from "vue"
import Vuex from "vuex"
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex);
let gou=new Vuex.Store({
    plugins: [createPersistedState()],
    // 状态
    state:{
       gou:[],
       ma:"",
       img:""
    //    qing:[]
    },
    // 计算属性
    getters:{
      zong(state){
        let zj=0;
        state.gou.forEach((v)=>{
            zj+=v.price*v.num
        })
            return zj
      },
      toNum(state){
          let zs=0;
          state.gou.forEach((v)=>{
              zs+=v.num
        })
        return zs
      }
    },
    // 修改
    mutations:{
        add(state,item){
            state.gou.push({name:item.name,price:item.specfoods[0].price,num:1})
        },
        jia(state,i){
            state.gou[i].num++
        },
        jian(state,i){
           state.gou[i].num--;
        },
        shan(state,i){
            state.gou.splice(i,1)   
        },
        empty(state){
            state.gou=[]
        },
        sub(state,i){
           state.gou[i].num--;
        },
        plu(state,item){
           state.gou.push({name:item.name,price:item.specfoods[0].price,num:1})
        },
         fang(state,v){
            state.ma=v
        },
        tui(state){
            state.ma=""
        },
        tu(state,v){
            state.img=v
        }
    },
    actions:{
      
    },
   
    
})
export default gou;


